import React, { useContext, useState } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { Card, CardContent, Checkbox, Divider, FormControlLabel, Stack, Typography } from '@mui/material';
import { cleanUnderscore, getBitIndex, getCoinsArray, notateNumber, prefix } from '@utility/helpers';
import Tooltip from 'components/Tooltip';
import { CardTitleAndValue, MissingData } from '@components/common/styles';
import { isGodEnabledBySorcerer } from '../../../parsers/lab';
import { NextSeo } from 'next-seo';
import { isCompanionBonusActive } from '../../../parsers/misc';
import { getMinorDivinityBonus } from '../../../parsers/divinity';
import CoinDisplay from '../../../components/common/CoinDisplay';
const Divinity = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    deities,
    linkedDeities,
    unlockedDeities,
    godRank
  } = state?.account?.divinity || {};
  const [showCost, setShowCost] = useState(false);
  if (!state?.account?.divinity) return <MissingData name={'divinity'} />;
  return <>
    <NextSeo title="Divinity | Idleon Toolbox" description="Keep track of your characters' gods connections and upgrades" data-sentry-element="NextSeo" data-sentry-source-file="divinity.jsx" />
    <CardTitleAndValue title={'God Rank'} value={godRank || 1} data-sentry-element="CardTitleAndValue" data-sentry-source-file="divinity.jsx" />
    <FormControlLabel control={<Checkbox name={'mini'} checked={showCost} size={'small'} onChange={() => setShowCost(!showCost)} />} label={'Show upgrade cost'} data-sentry-element="FormControlLabel" data-sentry-source-file="divinity.jsx" />
    <Stack my={2} direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="divinity.jsx">
      {deities?.map(({
        name,
        rawName,
        majorBonus,
        minorBonus,
        blessing,
        blessingMultiplier,
        blessingBonus,
        cost,
        level
      }, godIndex) => {
        const hasLinks = state?.characters?.some((character, index) => isCompanionBonusActive(state?.account, 0) || linkedDeities?.[index] === godIndex || isGodEnabledBySorcerer(character, godIndex));
        const highestDivinityCharacter = state?.characters?.reduce((prev, curr) => {
          const prevBonus = prev?.skillsInfo?.divinity?.level;
          const currBonus = curr?.skillsInfo?.divinity?.level;
          return prevBonus > currBonus ? prev : curr;
        }, state?.characters?.[0]);
        return <Card sx={{
          width: 300
        }} key={rawName} variant={godIndex < unlockedDeities ? 'elevation' : 'outlined'}>
          <CardContent>
            <Stack alignItems={'center'} gap={1}>
              <img src={`${prefix}data/${rawName}.png`} alt="" />
              <Stack gap={1} justifyContent={'space-between'} sx={{
                minHeight: 250
              }}>
                <Stack>
                  <Typography>{name}</Typography>
                  <Typography variant={'body2'}>Lv. {level} / 100</Typography>
                  <Divider sx={{
                    my: 2
                  }} />
                  <Typography variant={'body1'}>
                    Blessing: {cleanUnderscore(blessing.replace(/{/g, blessingBonus))}
                  </Typography>
                  {godIndex === 2 ? <Typography variant={'caption'}>* inaccurate</Typography> : null}
                  {cost?.cost !== 'MAX' && showCost ? <>
                    <Cost title={'Cost'} {...cost} cost={cost?.cost} />
                    <Cost title={'Next Level Cost'} {...cost} cost={cost?.nextLevelCost} />
                    <Cost title={'Cost To Max'} {...cost} cost={cost?.costToMax} />
                  </> : null}
                  <Divider sx={{
                    my: 2
                  }} />
                  <Typography sx={{
                    minHeight: 100
                  }} variant={'body1'}>{cleanUnderscore(majorBonus)}</Typography>
                </Stack>
                {hasLinks ? <>
                  <Divider sx={{
                    my: 2
                  }} />
                  <Stack direction={'row'} flexWrap={'wrap'} mt={'auto'}>
                    {state?.characters?.map(({
                      classIndex,
                      name,
                      deityMinorBonus = 0,
                      divStyle,
                      secondLinkedDeityIndex,
                      secondDeityMinorBonus = 0,
                      playerId
                    }, index) => {
                      const compBonus = isCompanionBonusActive(state?.account, 0) && blessingBonus > 0;
                      const isLinked = compBonus || linkedDeities?.[index] === godIndex;
                      const isSecondLinked = compBonus || secondLinkedDeityIndex === godIndex;
                      if (godIndex === 6 && compBonus && highestDivinityCharacter?.playerId !== playerId) return null;
                      return compBonus || isLinked || isSecondLinked ? <Tooltip title={<CharDeityDetails name={name} divStyle={divStyle} bonus={minorBonus.replace(/{/g, isLinked ? getMinorDivinityBonus(state?.characters?.[index], state?.account, godIndex).toFixed(2) : isSecondLinked ? secondDeityMinorBonus.toFixed(2) : 0)} />} key={name}>
                          <img src={`${prefix}data/ClassIcons${classIndex}.png`} alt="" />
                        </Tooltip> : null;
                    })}
                  </Stack>
                </> : null}
              </Stack>
            </Stack>
          </CardContent>
        </Card>;
      })}
    </Stack>
  </>;
};
const Cost = ({
  type,
  cost,
  title
}) => {
  const currencyIcon = type === 'bits' ? `etc/Bits_${getBitIndex(cost)}` : type === 'sailingGold' ? 'data/SailT0' : 'etc/Particle';
  return type !== 'coins' ? <Stack alignItems={'center'} direction={'row'} gap={1} mt={1} data-sentry-element="Stack" data-sentry-component="Cost" data-sentry-source-file="divinity.jsx">
    <Typography variant={'body2'} data-sentry-element="Typography" data-sentry-source-file="divinity.jsx">
      {title}: {cost === 'MAX' ? cost : notateNumber(cost, type === 'bits' ? 'bits' : 'Big')}
    </Typography>
    <img src={`${prefix}${currencyIcon}.png`} alt={''} />
  </Stack> : <CoinDisplay title={title} noShadow centered={false} money={getCoinsArray(cost)} data-sentry-element="CoinDisplay" data-sentry-component="Cost" data-sentry-source-file="divinity.jsx" />;
};
const CharDeityDetails = ({
  name,
  bonus,
  divStyle
}) => {
  return <>
    <Typography sx={{
      fontWeight: 'bold'
    }} data-sentry-element="Typography" data-sentry-source-file="divinity.jsx">{name}</Typography>
    <Typography data-sentry-element="Typography" data-sentry-source-file="divinity.jsx">Minor bonus: {cleanUnderscore(bonus)}</Typography>
    <Typography mt={1} sx={{
      fontWeight: 'bold'
    }} data-sentry-element="Typography" data-sentry-source-file="divinity.jsx">Style: {divStyle?.name}</Typography>
    <Typography data-sentry-element="Typography" data-sentry-source-file="divinity.jsx">{cleanUnderscore(divStyle?.description.replace(/@/, ''))}</Typography>
  </>;
};
export default Divinity;